import { createStore } from "@stencil/store";

export const login = createStore({} as LoginData);

export interface LoginData {
    strings: LoginStringsData;
    register: string;
    login: string;
    forgot: string;

    externalLogin: string;
    externalRegister: string;
    
    google: LoginExternalData | false;
    facebook: LoginExternalData | false;
}

export interface LoginStringsData {
    header: string;
    email: string;
    emailPlaceholder: string;
    password: string;
    passwordPlaceholder: string;
    forgotPassword: string;
    forgotMessage: string;
    forgotSubmit: string;
    register: string;
    login: string;
    externalLogin: string;
    externalLoginError: string;
}

export interface LoginExternalData {
    name: string,
    link: string,
}

