import { Component, h, Prop, Build, Element } from '@stencil/core';
import { login, LoginData } from "../../../global/data/login";
import { loadCommonData } from '../../../global/functions/loadCommon';
import { formfetch } from "../../../global/functions/fetch";

@Component({
    tag: 'ks-page-login',
    styleUrl: 'login.css'
})
export class PageLogin {
    @Element() root: HTMLElement;

    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() loginData: string;

    componentWillLoad() {
        const loginDataElement = document.getElementById(this.loginData);
		const loginData = JSON.parse(loginDataElement.innerHTML) as LoginData;

        Object.keys(loginData).map(key => {
			login.set(key as keyof LoginData, loginData[key]);
		});

        loadCommonData(this.commonData, this.commonDynamicData, Build);
    }

    componentDidLoad() {
        if(login.get("externalLogin") && login.get("externalRegister"))
            this.externalLogin(login.get("externalLogin"), login.get("externalRegister"));
    }

    showForgot() {
        const dialog = this.root.querySelector("ks-dialog.forgot-dialog") as HTMLKsDialogElement;
        dialog.show();
    }

	render() {
        const strings = login.get("strings");
        const google = login.get("google");
        const facebook = login.get("facebook");

		return <ks-page-base loadCommon={false} skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            <div class="background">
                <ks-simpleform class="card" action={login.get("login")} method="post">
                    <h1>{strings.header}</h1>

                    <ks-input-text required email name="email"
                        label={strings.email} icon={"at-sign"} right-icon
                        placeholder={strings.emailPlaceholder}
                    />

                    <ks-input-text required password name="haslo" 
                        label={strings.password} icon={"key"} right-icon
                        placeholder={strings.passwordPlaceholder}
                    />

                    <span class="forgot" onClick={()=>this.showForgot()}>{strings.forgotPassword}</span>

                    <ks-button submit class="login" primary name={strings.login} round />

                    {google || facebook ?
                        <div class="externalButtons">
                            { google ? 
                                <ks-button nofollow round icon="google" class="external"
                                    link={google.link} 
                                    name={strings.externalLogin}  
                                />
                            : null }

                            { facebook ? 
                                <ks-button nofollow round icon="facebook" class="external"
                                    link={facebook.link}
                                    name={strings.externalLogin} 
                                />
                            : null }
                        </div>
                    : null}

                    <ks-button nofollow link={login.get("register")} class="register" border narrow name={strings.register} round />
                </ks-simpleform>

                <ks-dialog class="forgot-dialog" dark>
                    <ks-simpleform action={login.get("forgot")} method="post">
                        <p>{strings.forgotMessage}</p>
                        <ks-input-text email name="emailprzypomnienie"
                            label={strings.email} icon={"at-sign"} right-icon
                            placeholder={strings.emailPlaceholder}
                        />
                        <ks-button class="forgotSubmit" border narrow name={strings.forgotSubmit} round />
                    </ks-simpleform>
                </ks-dialog>

                <ks-overlay dark close={false} class="loading">
                    <ks-loader />
                </ks-overlay>
            </div>
        </ks-page-base>;
	}

    externalLogin(loginapi: string, registerapi: string) {
        const errorpopup = document.querySelector("ks-error-popup") as HTMLKsErrorPopupElement;
        const overlay = this.root.querySelector("ks-overlay.loading") as HTMLKsOverlayElement;

        overlay.show();
        const translations = login.get("strings");

        formfetch(loginapi, {data: "social"})
        .then(data => data.text())
        .then(data => {
            if(data.includes("noaccount"))
                window.location.replace(registerapi);

            else if ( data.length > 10 ) {   
                overlay.hide();
                errorpopup.show(new Error(data));
           } else {
                overlay.hide();
                errorpopup.show(new Error(translations.externalLoginError));
           }
        });
    }
}




