ks-page-login .background {
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: white;
    padding: 80px 20px;
}

ks-page-login .card {
    width: 100%;
    max-width: 400px;

    background-color: white;
}

ks-page-login h1 {
    text-align: center;
    margin-bottom: 10px;
}

ks-page-login .forgot {
    float: right;
    cursor: pointer;
    margin-top: -10px;

    transition: opacity 0.3s ease;
}

ks-page-login .forgot:hover {
    opacity: 0.7;
}

ks-page-login .forgot:active {
    opacity: 0.5;
}

ks-page-login .login {
    margin-top: 50px;
}

ks-page-login .login > * {
    background: rgb(24,141,219);
    background: linear-gradient(321deg, rgba(24,141,219,1) 0%, rgba(0,26,78,1) 100%);

    transition: opacity 0.3s ease;
}

ks-page-login .login > *:hover {
    opacity: 0.9;
}

ks-page-login .login > *:active {
    opacity: 0.8;
}

ks-page-login .register {
    margin: 10px auto 0 auto;
    width: 90%;
}

ks-page-login .forgot-dialog p {
    text-align: center;
}

ks-page-login .forgotSubmit {
    margin: 20px 0 20px 0;
}

ks-page-login .externalButtons {
    display: flex;
    margin-top: 10px;
    margin-bottom: 20px;
    gap: 5px;
    align-items: center;
    justify-content: center;
}

ks-page-login .external {
    width: 100%;
}

ks-page-login .external > * {
    background: linear-gradient(321deg, rgb(77 77 77) 0%, rgba(26,26,26,1) 100%);
    transition: opacity 0.3s ease;
}

ks-page-login .external:last-child > * {
    background: linear-gradient(30deg, rgb(77 77 77) 0%, rgba(26,26,26,1) 100%);
}

ks-page-login .external > *:hover {
    opacity: 0.9;
}

ks-page-login .external > *:active {
    opacity: 0.8;
}